import React from 'react';
//import ReactDOM from 'react-dom';
import logo from "../img/header-logo.png";
import "../css/style-product-card.css";
import lady_shoes_1 from '../img/product-list__pic_1.jpg';
//import Catalogue from './Catalogue.js';
import MainPage from './main-page.js';
import ProductCard from './ProductCard.js';
//import Order from './Order.js';
//import Favourite from './Favourite.js';
import {headerHiddenPanelProfileVisibility, headerHiddenPanelBasketVisibility, headerMainSearchVisibility, mainSubmenuVisibility} from '../js/script.js';


import {
    HashRouter as Router,
    Route, Link, NavLink, Switch
} from 'react-router-dom';

class Header extends React.Component {
  render () {
  	return (
      <Router>
    		<header className="header">
          <div className="top-menu">
            <div className="wrapper">
              <ul className="top-menu__items">
                <li className="top-menu__item">
                  <a href="#">Возврат</a>
                </li>
                <li className="top-menu__item">
                  <a href="#">Доставка и оплата</a>
                </li>
                <li className="top-menu__item">
                  <a href="#">О магазине</a>
                </li>
                <li className="top-menu__item">
                  <a href="#">Контакты</a>
                </li>
                <li className="top-menu__item">
                  <a href="#">Новости</a>
                </li>
              </ul>
            </div>
          </div>
          <div className="header-main">
            <div className="header-main__wrapper wrapper">
              <div className="header-main__phone">
                <a href="tel:+7-495-790-35-03">+7 495 79 03 5 03</a>
                <p>Ежедневно: с 09-00 до 21-00</p>
              </div>
              <div className="header-main__logo">
                <NavLink to='/'>
                  <h1>
                    <img src={logo} alt="logotype"/>
                  </h1>
                </NavLink>
                <p>Обувь и аксессуары для всей семьи</p>
              </div>
              <div className="header-main__profile">
                <div className="header-main__pics">
                  <div className="header-main__pic header-main__pic_search" onClick = {headerMainSearchVisibility}>

                  </div>
                  <div className="header-main__pic_border"></div>
                  <div className="header-main__pic header-main__pic_profile" onClick = {headerHiddenPanelProfileVisibility}>
                    <div className="header-main__pic_profile_menu"></div>
                  </div>

                  <div className="header-main__pic_border"></div>
                        <div className="header-main__pic header-main__pic_basket" onClick = {headerHiddenPanelBasketVisibility}> 
                              <div className="header-main__pic_basket_full">1</div>
                              <div className="header-main__pic_basket_menu"></div>
                        </div>       
                  </div>

                <form className="header-main__search" action="#">
                  <input placeholder="Поиск"/>
                  <i className="fa fa-search" aria-hidden="true"></i>
                </form>
              </div>

            </div>
            <div className="header-main__hidden-panel hidden-panel">
              <div className="hidden-panel__profile">
                <a href="#">Личный кабинет</a>
                <NavLink to="/favourite">
                  <i className="fa fa-heart-o" aria-hidden="true"></i>Избранное</NavLink>
                <a href="#">Выйти</a>
              </div>
              <div className="hidden-panel__basket basket-dropped">
                <div className="basket-dropped__title">В вашей корзине:</div>
                <div className="basket-dropped__product-list product-list">
                  <div className="product-list__item">
                    <NavLink to="/productcard" className="product-list__pic">
                      <img src={lady_shoes_1} alt="product"/></NavLink>
                    <NavLink to="/productcard" className="product-list__product">Ботинки женские, Baldinini</NavLink>
                    <div className="product-list__fill"></div>
                    <div className="product-list__price">12 360
                      <i className="fa fa-rub" aria-hidden="true"></i>
                    </div>
                    <div className="product-list__delete">
                      <i className="fa fa-times" aria-hidden="true"></i>
                    </div>
                  </div>

                  <div className="product-list__item">
                    <NavLink to="productcard" className="product-list__pic">
                      <img src={lady_shoes_1} alt="product"/> </NavLink>
                    <NavLink to="/productcard" className="product-list__product">Ботинки женские, Baldinini</NavLink>
                    <div className="product-list__fill"></div>
                    <div className="product-list__price">12 360
                      <i className="fa fa-rub" aria-hidden="true"></i>
                    </div>
                    <div className="product-list__delete">
                      <i className="fa fa-times" aria-hidden="true"></i>
                    </div>
                  </div>
                  <div className="product-list__item">
                    <NavLink to="/productcard" className="product-list__pic">
                      <img src={lady_shoes_1} alt="product"/> </NavLink>
                    <NavLink to="/productcard" className="product-list__product">Ботинки женские, Baldinini</NavLink>
                    <div className="product-list__fill"></div>
                    <div className="product-list__price">12 360
                      <i className="fa fa-rub" aria-hidden="true"></i>
                    </div>
                    <div className="product-list__delete">
                      <i className="fa fa-times" aria-hidden="true"></i>
                    </div>
                  </div>
                  <div className="product-list__item">
                    <NavLink to="/productcard" className="product-list__pic">
                      <img src={lady_shoes_1} alt="product"/> </NavLink>
                    <NavLink to="/productcard" className="product-list__product">Ботинки женские, Baldinini</NavLink>
                    <div className="product-list__fill"></div>
                    <div className="product-list__price">12 360
                      <i className="fa fa-rub" aria-hidden="true"></i>
                    </div>
                    <div className="product-list__delete">
                      <i className="fa fa-times" aria-hidden="true"></i>
                    </div>
                  </div>

                </div>
                <NavLink className="basket-dropped__order-button" to="/Order">Оформить заказ</NavLink>
              </div>
            </div>
          </div>
          <nav className="main-menu">
            <div className="wrapper">
              <ul className="main-menu__items" onClick = {() => {mainSubmenuVisibility.bind(this)}}>
                <li className="main-menu__item main-menu__item_sales" >
                  <NavLink to="/catalogue">Акции</NavLink>
                </li>
                <li className="main-menu__item main-menu__item_women">
                  <NavLink to="/catalogue">Женская обувь</NavLink>
                </li>
                <li className="main-menu__item main-menu__item_men">
                  <NavLink to="/catalogue">Мужская обувь</NavLink>
                </li>
                <li className="main-menu__item main-menu__item_kids">
                  <NavLink to="/catalogue">Детская обувь</NavLink>
                </li>
                <li className="main-menu__item main-menu__item_accessories">
                  <NavLink to="/catalogue">Аксессуары</NavLink>
                </li>
                <li className="main-menu__item main-menu__item_home">
                  <NavLink to="/catalogue">Для дома</NavLink>
                </li>
                <li className="main-menu__item main-menu__item_brands">
                  <NavLink to="/catalogue">Бренды</NavLink>
                </li>
                <li className="main-menu__item main-menu__item_new">
                  <NavLink to="/catalogue">Новинки</NavLink>
                </li>
              </ul>
            </div>
          </nav>
          <div className="dropped-menu">
            <div className="wrapper">
              <div className="dropped-menu__lists dropped-menu__lists_women">
                <h3 className="dropped-menu__list-title">Повод:</h3>
                <ul className="dropped-menu__list">
                  <li className="dropped-menu__item">
                    <a href="#">Офис</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Вечеринка</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Свадьба</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Спорт</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Море</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Дом</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Повседневное</a>
                  </li>
                </ul>
              </div>
              <div className="dropped-menu__lists dropped-menu__lists_three-coloumns">
                <h3 className="dropped-menu__list-title">Категории:</h3>
                <ul className="dropped-menu__list">
                  <li className="dropped-menu__item">
                    <a href="#">Балетки</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Босоножки</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Ботильоны</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Ботинки</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Ботфорты</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Галоши</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Кеды и кроссовки</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Мокасины</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Полусапоги</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Резиновые сапоги</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Сабо</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Сапоги</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Сникерсы</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Тапочки</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Туфли</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Шлёпанцы и вьетнамки</a>
                  </li>
                </ul>
              </div>
              <div className="dropped-menu__lists">
                <h3 className="dropped-menu__list-title">Сезон:</h3>
                <ul className="dropped-menu__list">
                  <li className="dropped-menu__item">
                    <a href="#">Зима</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Весна</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Лето</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Осень</a>
                  </li>
                </ul>
              </div>
              <div className="dropped-menu__lists">
                <h3 className="dropped-menu__list-title">Бренды:</h3>
                <ul className="dropped-menu__list">
                  <li className="dropped-menu__item">
                    <a href="#">Albano</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Ballin</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Baldinini</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Damlax</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Pegia</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Renzi</a>
                  </li>
                  <li className="dropped-menu__item">
                    <a href="#">Все</a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </header>
      </Router>
  	);
  }
}

export default Header;


